import React from 'react';
import { Route, Switch, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Header from 'components/Header/index';
import Sidebar from 'containers/SideNav/index';
import Footer from 'components/Footer';
import Tour from '../components/Tour/index';


import ColorOption from 'containers/Customizer/ColorOption';
import {
  ABOVE_THE_HEADER,
  BELOW_THE_HEADER,
  COLLAPSED_DRAWER,
  FIXED_DRAWER,
  HORIZONTAL_NAVIGATION,
} from 'constants/ActionTypes';
import { isIOS, isMobile } from 'react-device-detect';
import asyncComponent from '../util/asyncComponent';
import TopNav from 'components/TopNav';

import AddNewTour from './routes/ToursManagement/AddNewTour';
import Tours from './routes/ToursManagement/Tours';
import Dashboard from './routes/dashboard/index';
import AddCity from './routes/CityAndSubCityManagement/CityManagement/addcity';
import ListCities from './routes/CityAndSubCityManagement/CityManagement/listcities';
import AddSubCity from './routes/CityAndSubCityManagement/SubCityManagement/addsubcity';
import ListSubCities from './routes/CityAndSubCityManagement/SubCityManagement/listsubcities';


class App extends React.Component {

  render() {
    const { match, drawerType, navigationStyle, horizontalNavPosition } = this.props;
    const drawerStyle = drawerType.includes(FIXED_DRAWER) ? 'fixed-drawer' : drawerType.includes(COLLAPSED_DRAWER) ? 'collapsible-drawer' : 'mini-drawer';

    //set default height and overflow for iOS mobile Safari 10+ support.
    if (isIOS && isMobile) {
      document.body.classList.add('ios-mobile-view-height')
    }
    else if (document.body.classList.contains('ios-mobile-view-height')) {
      document.body.classList.remove('ios-mobile-view-height')
    }

    return (
      <div className={`app-container ${drawerStyle}`}>
        <Tour />

        <Sidebar />
        <div className="app-main-container">
          <div
            className={`app-header ${navigationStyle === HORIZONTAL_NAVIGATION ? 'app-header-horizontal' : ''}`}>
            {(navigationStyle === HORIZONTAL_NAVIGATION && horizontalNavPosition === ABOVE_THE_HEADER) &&
              <TopNav styleName="app-top-header" />}
            <Header />
            {(navigationStyle === HORIZONTAL_NAVIGATION && horizontalNavPosition === BELOW_THE_HEADER) &&
              <TopNav />}
          </div>

          <main className="app-main-content-wrapper">
            <div className="app-main-content">
              <Switch>
                <Route path={`${match.url}/dashboard`} component={Dashboard} />
                <Route path={`${match.url}/tour/add-new-tour`} component={AddNewTour} />
                <Route path={`${match.url}/tour/list-tours`} component={Tours} />
                <Route path={`${match.url}/city/list-city`} component={ListCities} />
                <Route path={`${match.url}/city/add-city`} component={AddCity} />
                <Route path={`${match.url}/subcity/list-sub-city`} component={ListSubCities} />
                <Route path={`${match.url}/subcity/add-sub-city`} component={AddSubCity} />                
                <Route path={`${match.url}/sample-page`}
                  component={asyncComponent(() => import('./routes/SamplePage'))} />
                <Route component={asyncComponent(() => import('components/Error404'))} />
              </Switch>
            </div>
            <Footer />
            <ColorOption />
          </main>
        </div>
      </div>
    );
  }
}


const mapStateToProps = ({ settings }) => {
  const { drawerType, navigationStyle, horizontalNavPosition } = settings;
  return { drawerType, navigationStyle, horizontalNavPosition }
};
export default withRouter(connect(mapStateToProps)(App));