import React from 'react';
import CardBox from 'components/CardBox/index';
import IntlMessages from 'util/IntlMessages';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import CKEditor from 'react-ckeditor-component';
import Autocomplete from '@material-ui/lab/Autocomplete';
import AccountCircle from '@material-ui/icons/AccountCircle';
import InputAdornment from '@material-ui/core/InputAdornment';
import { TourModel, ListCity, ListSubCity, ListAgencies, ListTourGuide, ListAttributes } from './data'




class AddNewTour extends React.Component {
    constructor() {
        super();

        this.updateContent = this.updateContent.bind(this);
        this.state = {
            userName: '',
            password: '',
            faAgencyName: '',
            enAgencyName: '',
            faFullNameAgencyManager: '',
            enFullNameAgencyManager: '',
            email: '',
            managerNationalCode: '',
            shaba: '',
            phoneNumber1: '',
            phoneNumber2: '',
            mobileNumber: '',
            faxNumber: '',
            emergencyNumber: '',
            content: '',
            countries: [],
            states: [],
            selectedCountry: '--Choose Country--',
            selectedState: '--Choose State--'
        }
        this.changeCountry = this.changeCountry.bind(this);
        this.changeState = this.changeState.bind(this);
    }


    changeCountry(event) {
        this.setState({ selectedCountry: event.target.value });
        this.setState({ states: this.state.countries.find(cntry => cntry.name === event.target.value).states });
    }

    changeState(event) {
        this.setState({ selectedState: event.target.value });

    }

    updateContent(newContent) {
        this.setState({
            content: newContent
        })
    }
    onChange(evt) {
        const newContent = evt.editor.getData();
        this.setState({
            content: newContent
        })
    }

    onBlur(evt) {
        console.log('onBlur event called with event info: ', evt);
    }

    afterPaste(evt) {
        console.log('afterPaste event called with event info: ', evt);
    }
    handleUserNameChange = event => {
        this.setState({ userName: event.target.value })
    }
    handlePasswordChange = event => {
        this.setState({ password: event.target.value })
    }
    handleFaAgencyNameChange = event => {
        this.setState({ faAgencyName: event.target.value })
    }
    handleEnAgencyNameChange = event => {
        this.setState({ enAgencyName: event.target.value })
    }
    handleFaFullNameAgencyManagerChange = event => {
        this.setState({ faFullNameAgencyManager: event.target.value })
    }
    handleEnFullNameAgencyManagerChange = event => {
        this.setState({ enFullNameAgencyManager: event.target.value })
    }
    handleEmailChange = event => {
        this.setState({ email: event.target.value })
    }
    handleManagerNationalCodeChange = event => {
        this.setState({ managerNationalCode: event.target.value })
    }
    handleShabaChange = event => {
        this.setState({ shaba: event.target.value })
    }
    handlephoneNumber1Change = event => {
        this.setState({ phoneNumber1: event.target.value })
    }
    handlephoneNumber2Change = event => {
        this.setState({ phoneNumber2: event.target.value })
    }
    handleMobileNumberChange = event => {
        this.setState({ mobileNumber: event.target.value })
    }
    handleFaxNumberChange = event => {
        this.setState({ faxNumber: event.target.value })
    }
    handleEmergencyNumberChange = event => {
        this.setState({ emergencyNumber: event.target.value })
    }

    render() {

        return (
            <div className="app-wrapper animated slideInUpTiny animation-duration-3">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour" />}
                                headerOutside>
                                <h4 className="text-primary"><IntlMessages id="tourManagement.tour.addNewTour" /></h4>
                            </CardBox>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-6">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.addNewTour" />}>
                                <hr />
                                <div className="row">
                                    <div className="col-md">
                                        <TextField
                                            size="small"
                                            margin="normal"
                                            fullWidth
                                            id="userName"
                                            value={this.state.userName}
                                            onChange={this.handleUserNameChange}
                                            label={<IntlMessages id="tourManagement.tour.addNewTour.username" />}
                                            variant="outlined"
                                        />
                                    </div>
                                    <div className="col-md">
                                        <TextField
                                            size="small"
                                            fullWidth
                                            margin="normal"
                                            id="password"
                                            value={this.state.password}
                                            onChange={this.handlePasswordChange}
                                            label={<IntlMessages id="tourManagement.tour.addNewTour.password" />}
                                            type="password"
                                            autoComplete="current-password"
                                            variant="outlined"
                                        />
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.informations" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="faAgencyName"
                                                value={this.state.faAgencyName}
                                                onChange={this.handleFaAgencyNameChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.faAgencyName" />}
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="enAgencyName"
                                                value={this.state.enAgencyName}
                                                onChange={this.handleEnAgencyNameChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.enAgencyName" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                value={this.state.faFullNameAgencyManager}
                                                onChange={this.handleFaFullNameAgencyManagerChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.faFullNameAgencyManager" />}
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                value={this.state.enFullNameAgencyManager}
                                                onChange={this.handleEnFullNameAgencyManagerChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.enFullNameAgencyManager" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="email"
                                                value={this.state.email}
                                                onChange={this.handleEmailChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.email" />}
                                                variant="outlined"
                                                type="email"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="managerNationalCode"
                                                value={this.state.managerNationalCode}
                                                onChange={this.handleManagerNationalCodeChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.managerNationalCode" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                value={this.state.shaba}
                                                onChange={this.handleShabaChange}
                                                id="shaba"
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.shaba" />}
                                                variant="outlined"
                                                placeholder="IRxxxxxxxxxxxxxxxxxxxxxxxx"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                value={this.state.phoneNumber1}
                                                onChange={this.handlephoneNumber1Change}
                                                id="phoneNumber1"
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.phoneNumber1" />}
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                value={this.state.phoneNumber2}
                                                onChange={this.handlephoneNumber2Change}
                                                id="phoneNumber2"
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.phoneNumber2" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                value={this.state.mobileNumber}
                                                onChange={this.handleMobileNumberChange}
                                                id="mobileNumber"
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.mobileNumber" />}
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="faxNumber"
                                                value={this.state.faxNumber}
                                                onChange={this.handleFaxNumberChange}
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.faxNumber" />}
                                                variant="outlined"

                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                value={this.state.emergencyNumber}
                                                onChange={this.handleEmergencyNumberChange}
                                                id="emergencyNumber"
                                                label={<IntlMessages id="tourManagement.tour.addNewTour.emergencyNumber" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.geolocationInformations" />}>
                                <hr />
                                <div>


                                    <div className="row">
                                        <div className="col-md">

                                            <FormControl variant="outlined" fullWidth size="small">
                                                <InputLabel id="demo-simple-select-outlined-label">Age</InputLabel>
                                                <Select
                                                    value={this.state.selectedCountry}
                                                    onChange={this.changeCountry}
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"
                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    {this.state.countries.map((e, key) => {
                                                        return <MenuItem value={e.name} key={key}>{e.name}</MenuItem>;
                                                    })}
                                                </Select>
                                            </FormControl>
                                        </div>
                                        <div className="col-md">

                                            <FormControl variant="outlined" fullWidth size="small">
                                                <InputLabel id="demo-simple-select-outlined-label">Age</InputLabel>
                                                <Select
                                                    value={this.state.selectedState}
                                                    onChange={this.changeState}
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"

                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    {this.state.states.map((e, key) => {
                                                        return <MenuItem value={e} key={key}>{e}</MenuItem>;
                                                    })}
                                                </Select>
                                            </FormControl>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                fullWidth
                                                margin="normal"
                                                id="outlined-password-input"
                                                label="Password"
                                                type="password"
                                                autoComplete="current-password"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                fullWidth
                                                id="standard-multiline-static"
                                                label="Multiline"
                                                multiline
                                                rows={4}
                                                variant="outlined"
                                                margin="normal"

                                            />
                                        </div>



                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <CKEditor
                                                activeClass="p10"
                                                content={this.state.content}
                                                events={{
                                                    'blur': this.onBlur.bind(this),
                                                    'afterPaste': this.afterPaste.bind(this),
                                                    'change': this.onChange.bind(this)
                                                }}
                                            />

                                        </div>

                                    </div>
                                </div>
                            </CardBox>
                        </div>

                        <div className="col-md-6">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.agencyLogo" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.agencyLicense" />}>
                                <hr />
                                <div className="row">
                                    <div className="col-md">
                                        <TextField
                                            size="small"
                                            margin="normal"
                                            fullWidth
                                            id="outlined-basic"
                                            label="Outlined"
                                            variant="outlined"
                                        />
                                    </div>
                                    <div className="col-md">
                                        <TextField
                                            size="small"
                                            fullWidth
                                            margin="normal"
                                            id="outlined-password-input"
                                            label="Password"
                                            type="password"
                                            autoComplete="current-password"
                                            variant="outlined"
                                        />
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.vipWebsite" />}>
                                <hr />
                                <div className="row">
                                    <div className="col-md">
                                        <TextField
                                            size="small"
                                            margin="normal"
                                            fullWidth
                                            id="outlined-basic"
                                            label="Outlined"
                                            variant="outlined"
                                        />
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.agencySocialApp" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.appDownloadLinks" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md-8">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md-4">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-8">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                        <div className="col-md-4">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="outlined-basic"
                                                label="Outlined"
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="tourManagement.tour.addNewTour.agencyRelatedTags" />}>
                                <hr />
                                <div className="row">
                                    <div className="col-md">
                                        {/* <Autocomplete
                                            multiple
                                            id="tags-outlined"
                                            options={}
                                            getOptionLabel={(option) => option.title}
                                            defaultValue={""}
                                            filterSelectedOptions
                                            renderInput={(params) => (
                                                <TextField
                                                    {...params}
                                                    variant="outlined"
                                                    label="filterSelectedOptions"
                                                    placeholder="Favorites"
                                                />
                                            )}
                                        /> */}
                                    </div>
                                </div>
                            </CardBox>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default AddNewTour;