import React from 'react';
import Button from '@material-ui/core/Button';
import IntlMessages from 'util/IntlMessages';

const Footer = () => {
    return (
      <footer className="app-footer">
        <span className="d-inline-block">ParsAlpha.ir Team &copy; 2020</span>
        <Button
          href="http://parsalpha.ir"
          target="_blank"
          size="small"
          color="primary"
        ><IntlMessages id="parsalpha.salmansamian"/></Button>
      </footer>
    );
  }
;

export default Footer;
