import React from 'react';
import CardBox from 'components/CardBox/index';
import IntlMessages from 'util/IntlMessages';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogActions from '@material-ui/core/DialogActions';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import FormHelperText from '@material-ui/core/FormHelperText';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import CKEditor from 'react-ckeditor-component';
import Autocomplete from '@material-ui/lab/Autocomplete';
import AccountCircle from '@material-ui/icons/AccountCircle';
import InputAdornment from '@material-ui/core/InputAdornment';
import { CityModels, SubCtyModel } from '../data'
import { Button } from '@material-ui/core';




class AddSubCity extends React.Component {
    constructor() {
        super();

        this.state = {
            id: 1,
            FaName: '',
            EnName: 'Shiraz',
            City: {
                id: 1,
                FaName: '',
                EnName: 'Fars',
                DateInsert: ''
            },
            Status: 0,
            DateInsert: ''
        }

        this.CityFaName = '';
        this.state.Cities = CityModels;
        this.changeCity = this.changeCity.bind(this);
        this.state.opendialog = false;
    }

    changeCity(event) {
        this.setState({ CityFaName: event.target.value });
        this.setState({ states: this.state.Cities.find(c => c.FaName === event.target.value).states });
    }

    onBlur(evt) {
        console.log('onBlur event called with event info: ', evt);
    }

    afterPaste(evt) {
        console.log('afterPaste event called with event info: ', evt);
    }

    handleaddNewItemCity = event => {
        console.log('afterPaste event called with event info: ', event);
    }

    handCheckUniqeFaNameCity = event => {
        this.setState({ FaName: event.target.value })
    }

    handCheckUniqeEnNameCity = event => {
        this.setState({ EnName: event.target.value })
    }

    handleRequestClose = () => {
        this.setState({ opendialog: false });
    }


    render() {

        return (
            <div className="app-wrapper animated slideInUpTiny animation-duration-3">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="subcityManagement.subcity.titlefa" />}
                                headerOutside>
                                <h4 className="text-primary"><IntlMessages id="subcityManagement.subcity.titlefa" /></h4>
                            </CardBox>
                        </div>
                    </div>


                    <div className="row">
                        <div className="col-md-12">

                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="subcityManagement.subcity.selectcity" />}>
                                <FormControl variant="outlined" fullWidth size="small">
                                    <InputLabel id="demo-simple-select-outlined-label"></InputLabel>
                                    <Select
                                        value={this.state.CityFaName}
                                        onChange={this.changeCity}
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        label="">
                                        <MenuItem value={this.CityFaName}>
                                            <em> </em>
                                        </MenuItem>
                                        {this.state.Cities.map((e, key) => {
                                            return <MenuItem value={e.FaName} key={key}>{e.FaName}</MenuItem>;
                                        })}
                                    </Select>
                                </FormControl>
                            </CardBox>
                        </div>
                    </div>


                    <div className="row">

                        <div className="col-md-6">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="subcityManagement.subcity.titlefa" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="FaName"
                                                value={this.state.FaName}
                                                onChange={this.handCheckUniqeFaNameCity}
                                                label={<IntlMessages id="subcityManagement.subcity.titlefa" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                        </div>

                        <div className="col-md-6">
                            <CardBox styleName="col-12" cardStyle="p-3" heading={<IntlMessages id="subcityManagement.subcity.titleen" />}>
                                <hr />
                                <div>
                                    <div className="row">
                                        <div className="col-md">
                                            <TextField
                                                size="small"
                                                margin="normal"
                                                fullWidth
                                                id="EnName"
                                                value={this.state.EnName}
                                                onChange={this.handCheckUniqeEnNameCity}
                                                label={<IntlMessages id="cityManagement.city.titleen" />}
                                                variant="outlined"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </CardBox>
                        </div>

                    </div>

                    <div className="row">
                        <div className="col-md-12">

                            {/* <CardBox styleName="col-12" cardStyle="p-3" headerOutside>
                                <Button variant="raised" className="jr-btn bg-cyan text-white" onClick={this.handleaddNewItemCity}>
                                    <IntlMessages id="commoneManagement.addnewitem" />
                                </Button>
                            </CardBox> */}


                            <CardBox styleName="col-12" cardStyle="p-3">

                                <Button variant="raised" className="bg-primary text-white"
                                    onClick={() => this.setState({ opendialog: true })}>
                                    <IntlMessages id="commoneManagement.addnewitem" />
                                </Button>



                            </CardBox>



                            <Dialog open={this.state.opendialog} onClose={this.handleRequestClose}>
                                <DialogTitle>
                                    {"  "}
                                </DialogTitle>
                                <DialogContent>
                                    <DialogContentText>
                                              
                                        </DialogContentText>
                                </DialogContent>
                                <DialogActions>
                                    <Button onClick={this.handleRequestClose} color="secondary">
                                        
                                        </Button>
                                    <Button onClick={this.handleRequestClose} color="primary">
                                        
                                        </Button>
                                </DialogActions>
                            </Dialog>


                        </div>
                    </div>
                
                </div>
            </div>
        );
    }
}
export default AddSubCity;